html {
  height: 100%;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  margin: 0;
  padding: 0;
  height: 100%;
  body.dark-theme & {
    background: #242526;
    color: getColor('header-text');
  }
  body.light-theme & {
    background: getColor('bg-page');
  }
}

.page {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  body.dark-theme & {
    background: #242526;
  }
  body.light-theme & {
    background: getColor('bg-page');
  }
}

main {
  flex: 1 1 auto;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  padding: 0;
  margin: 0;
}

img {
  display: block;
  width: 100%;
  height: auto;
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}
