.section-movies {
  padding: 20px 0 0;
  body.dark-theme & {
    background: #242526;
  }
  body.light-theme & {
    background: getColor('header-text');
  }

  @include respond-to(tablet) {
    padding: 60px 0 0;
  }

  &__set {
    @include respond-to(tablet) {
      display: flex;
      flex-wrap: wrap;
      justify-content: start;
      margin: -15px;
    }
  }

  &__card {
    min-height: 440px;
    margin: 0 auto 20px;
    width: 100%;

    &:last-child {
      margin-bottom: 0;
    }

    @include respond-to(mobile) {
      width: 70%;
    }

    @include respond-to(tablet) {
      margin: 15px;
      flex-basis: calc((100% - 4 * 15px) / 2);
    }

    @include respond-to(desktop) {
      flex-basis: calc((100% - 6 * 15px) / 3);
    }
  }
}

.empty-library {
  width: 100%;
  font-size: 20px;
  font-weight: 500;
  text-align: center;
  margin-top: 3rem;
  body.dark-theme & {
    color: getColor('header-text');
  }
}

